import * as React from "react"
import PropTypes from "prop-types"
import { Switch } from "@material-ui/core"
import { useHttp } from "../../hooks/http.hook"
import "./control-block.css"
import Loader from "../loader"

const ControlBlock = ({ data, switchable }) => {
	const { request } = useHttp()
	const [loading, setLoading] = React.useState(true)

	const SendChangedOption = (value, key) => {
		try {
			request(`/api/link/change`, "POST", { value, key })
		} catch (e) {
			console.log(e)
		}
	}

	function chunk(arr, size) {
		const result = []
		for (let i = 0; i < Math.ceil(arr.length / size); i += 1) {
			result.push(arr.slice(i * size, i * size + size))
		}
		return result
	}
	const newData = chunk(data, 2)
	return (
		<div className="control-block">
			<div className="sensor-wrapper">
				{newData.map((array) => {
					if (array[0] === undefined) {
						return (
							<div key={Math.random() * 10 + 1} className="wrapper-container">
								<Loader />
							</div>
						)
					}
					return (
						<div key={Math.random() * 10 + 1} className="wrapper-container">
							{array.map((el) => {
								return (
									<div key={Math.random() * 10 + 1} className="sensor-info">
										<span className="info-text">{Object.values(el)[0]}</span>
										<span className="info-value">{Object.values(el)[1]}</span>
									</div>
								)
							})}
						</div>
					)
				})}
			</div>
			<div className="sensor-switcher">
				{switchable === undefined ? (
					<Loader />
				) : (
					switchable.map((el) => {
						if (el === undefined) {
							return null
						}
						return (
							<div key={el.key} className="sensor-switch">
								<span className="switch-text">{el.title}</span>
								<Switch
									onClick={() => SendChangedOption(el.key, !el.value)}
									checked={Boolean(el.value)}
								/>
							</div>
						)
					})
				)}
			</div>
		</div>
	)
}

ControlBlock.propTypes = {
	data: PropTypes.array.isRequired,
	switchable: PropTypes.array.isRequired,
}

export default ControlBlock
